import { TFunction } from "i18next";
import { useNavigate } from "react-router";
import { Variants, motion } from "framer-motion";
import { Tabs, Box, Tab } from "@mui/material";

export type TabItemType = {
	key: string;
	label: string;
};

interface TabItemProps extends TabItemType {
	index: number;
	[other: string]: any;
}

const TabItem = (props: TabItemProps) => {
	const { index, ...other } = props;

	return (
		<motion.div variants={other["variants"]}>
			<Tab
				className="py-24 whitespace-nowrap"
				{...other}
			/>
		</motion.div>
	);
};

TabItem.muiName = "Tab";

type CustomTabsProps = {
	tabValue: number;
	tabLabel: string;
	tabs: Array<TabItemType>;
	t: TFunction<string, undefined>;
	handleTabChange?: (newValue: number) => void;
	noScroll?: boolean;
};

const CustomTabs = (props: CustomTabsProps) => {
	const container: Variants = {
		show: { transition: { staggerChildren: 0.1 } }
	};
	const item: Variants = {
		hidden: { opacity: 0, y: -40 },
		show: { opacity: 1, y: 0 }
	};
	const { tabValue, tabLabel, tabs, t, handleTabChange, noScroll } = props;
	const navigate = useNavigate();

	const handleChange = (event: React.SyntheticEvent, newValue: number) => {
		if (handleTabChange) {
			handleTabChange(newValue);
		} else {
			navigate(`/${tabLabel}/${tabs[newValue].key}`);
		}
	};

	return (
		<motion.div
			variants={container}
			initial="hidden"
			animate="show"
		>
			<Box sx={{ borderBottom: 1, borderColor: "divider" }}>
				<Tabs
					value={tabValue}
					onChange={handleChange}
					aria-label="settings tab"
					variant={noScroll ? "standard" : "scrollable"}
					scrollButtons={true}
				>
					{tabs.map((tabItem, index) => (
						<TabItem
							key={tabItem.key}
							label={t(tabItem.label)}
							index={index}
							variants={item}
						/>
					))}
				</Tabs>
			</Box>
		</motion.div>
	);
};

export default CustomTabs;
