import { apiService as api } from "app/store/apiService";
import { IBusinessPhase, IBusinessPhaseEssentials, ITableBusinessPhase, ITableBusinessPhaseArg } from "./BusinessPhasesModel";

export const addTagTypes = ["businessPhase/fetchAll", "businessPhase/fetchOne"] as const;

const BusinessPhaseApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getAllBusinessPhases: build.query<GetBusinessPhasesApiResponse, GetBusinessPhasesApiArg>({
			query: ({ businessId, page, pageSize, sortBy, sortOrder, filter }) => {
				let params = "?";

				if (page) params += `page=${page}&`;
				if (pageSize) params += `pageSize=${pageSize}&`;
				if (sortBy) params += `sortBy=${sortBy}&`;
				if (sortOrder) params += `sortOrder=${sortOrder}&`;
				if (filter) params += `filter=${filter}`;

				return {
					url: `/business/getbusinessphases/${businessId}${params}`
				};
			},
			providesTags: ["businessPhase/fetchAll"]
		}),
		getOneBusinessPhase: build.query<GetBusinessPhaseApiResponse, GetBusinessPhaseApiArg>({
			query: (businessPhaseId) => ({
				url: `/business/getbusinessphasebyid/${businessPhaseId}`
			}),
			providesTags: ["businessPhase/fetchOne"]
		}),
		postBusinessPhase: build.mutation<PostBusinessPhaseApiResponse, PostBusinessPhaseApiArg>({
			query: (queryArg) => ({
				url: `/business/addbusinessphases`,
				method: "POST",
				data: queryArg
			}),
			invalidatesTags: ["businessPhase/fetchAll"]
		}),
		putBusinessPhase: build.mutation<PutBusinessPhaseApiResponse, PutBusinessPhaseApiArg>({
			query: (queryArg) => ({
				url: `/business/updatebusinessphases`,
				method: "PUT",
				data: queryArg
			}),
			invalidatesTags: ["businessPhase/fetchAll", "businessPhase/fetchOne"]
		}),
		deleteBusinessPhase: build.mutation<void, DeleteBusinessPhaseApiArg>({
			query: (businessPhaseId) => ({
				url: `/business/deletebusinessphase/${businessPhaseId}`,
				method: "DELETE"
			}),
			invalidatesTags: ["businessPhase/fetchAll"]
		})
	})
});

export default BusinessPhaseApi;

export type GetBusinessPhaseApiArg = string;
export type GetBusinessPhaseApiResponse = IBusinessPhase;
export type GetBusinessPhasesApiArg = ITableBusinessPhaseArg;
export type GetBusinessPhasesApiResponse = ITableBusinessPhase;
export type PutBusinessPhaseApiResponse = boolean;
export type PutBusinessPhaseApiArg = IBusinessPhase;
export type PostBusinessPhaseApiResponse = boolean;
export type PostBusinessPhaseApiArg = IBusinessPhaseEssentials;
export type DeleteBusinessPhaseApiArg = string;

export const { useGetOneBusinessPhaseQuery, useGetAllBusinessPhasesQuery, usePutBusinessPhaseMutation, usePostBusinessPhaseMutation, useDeleteBusinessPhaseMutation } = BusinessPhaseApi;

export type BusinessPhaseApiType = {
	[BusinessPhaseApi.reducerPath]: ReturnType<typeof BusinessPhaseApi.reducer>;
};
