import { Control, Controller, FieldErrors, FieldValues } from "react-hook-form";
import { SelectList } from "../custom-select-field/CustomSelectField";
import { BaseSyntheticEvent, Dispatch, SetStateAction, useEffect, useState } from "react";
import { Autocomplete, Box, CircularProgress, InputAdornment, TextField } from "@mui/material";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";

type CustomAutoCompleteProps = {
	control: Control<any, any>;
	name: string;
	label: string;
	errors: FieldErrors<FieldValues>;
	selectList?: Array<SelectList>;
	onScrollFunc?: () => void;
	onInputChange?: (value) => void;
	onSelectState?: Dispatch<SetStateAction<SelectList>>;
	selectedState?: SetStateAction<SelectList>;
	[other: string]: any;
};

const CustomAutoComplete = (props: CustomAutoCompleteProps) => {
	const { control, name, label, errors, selectList, onScrollFunc, onInputChange, onSelectState, selectedState, ...other } = props;
	const [inputValue, setInputValue] = useState("");

	const [customClass, setCustomClass] = useState("mt-12 shrink-0 ");

	useEffect(() => {
		if (other["dialog"]) {
			setCustomClass((state) => state + "w-full ");
			if (other["col2"]) {
				setCustomClass((state) => state + "col-span-4 md:col-span-2 ");
			}
		} else if (other["double"] && other["filter"]) {
			setCustomClass((state) => state + "w-1/2 ");
		} else if (other["double"]) {
			setCustomClass((state) => state + "w-[49%] ");
		} else if (other["filter"]) {
			setCustomClass((state) => state + "w-1/8 ");
		} else {
			setCustomClass((state) => state + "md:mx-6 w-full md:w-[45%] ");
		}

		return () => {
			setCustomClass("mt-12 shrink-0 ");
		};
	}, []);

	useEffect(() => {
		if (selectedState === null) {
			setInputValue("");
		}
	}, [selectedState]);

	return (
		<Controller
			name={name}
			control={control}
			render={({ field }) => (
				<Autocomplete
					{...field}
					inputValue={inputValue}
					className={customClass}
					noOptionsText={<CircularProgress size={16} />}
					options={selectList}
					autoHighlight
					disableClearable={!!other["disableClearable"]}
					disabled={!!other["disabled"]}
					defaultValue={other["defaultValue"] ? other["defaultValue"] : null}
					getOptionLabel={(option) => option.label}
					onChange={(event: any, newValue?: SelectList) => {
						field.onChange(newValue);
						onSelectState(newValue);
					}}
					freeSolo
					forcePopupIcon
					onInputChange={(e: BaseSyntheticEvent, newInputValue) => {
						onInputChange(newInputValue);
						setInputValue(newInputValue);
					}}
					isOptionEqualToValue={(option, value) => {
						return option.value === value.value || value === "";
					}}
					ListboxProps={{
						onScroll: (event) => {
							const listboxNode = event.currentTarget;
							if (listboxNode.scrollTop + listboxNode.clientHeight === listboxNode.scrollHeight) {
								onScrollFunc();
							}
						}
					}}
					renderOption={(props, option) => (
						<Box
							key={option.value}
							component="li"
							sx={{ "& > img": { mr: 2, flexShrink: 0 } }}
							{...props}
						>
							{option.label}
						</Box>
					)}
					renderInput={(params) => (
						<TextField
							{...params}
							label={label}
							aria-label={label}
							id={name}
							variant="outlined"
							error={!!errors[name]}
							helperText={other["double"] ? null : (errors[name]?.message as string) || " "}
							aria-required={other["required"] ? "true" : "false"}
							InputProps={{
								...params.InputProps,
								startAdornment: (
									<InputAdornment position="start">
										<FuseSvgIcon
											className="text-48"
											size={24}
											color="action"
										>
											material-outline:search
										</FuseSvgIcon>
									</InputAdornment>
								)
							}}
							{...other}
						/>
					)}
				/>
			)}
		/>
	);
};

export default CustomAutoComplete;
