import { ReactNode } from "react";
import { motion } from "framer-motion";
import { Box } from "@mui/system";

type CustomTabPanelProps = {
	children?: ReactNode;
	index: number;
	value: number;
	aria: string;
	[other: string]: any;
};

const CustomTabPanel = (props: CustomTabPanelProps) => {
	const { children, value, index, aria, ...other } = props;

	return (
		<motion.div
			initial={{ opacity: 0, x: -40 }}
			animate={{ opacity: 1, x: 0, transition: { delay: 0.5 } }}
			role="tabpanel"
			hidden={value !== index}
			id={`tabpanel-${index}`}
			aria-labelledby={`tab-${aria}`}
			{...other}
		>
			{value === index && (
				<Box>
					<div className="flex flex-col md:flex-row items-center justify-center flex-wrap gap-10 w-full">{children}</div>
				</Box>
			)}
		</motion.div>
	);
};

export default CustomTabPanel;
